








export default {
	props:{
		value:{
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			selectValue: 0
		}
	},
	mounted() {
		this.selectValue = this.value;
	},
	methods:{
		add(){
			this.selectValue ++;
			this.$emit('input',this.selectValue);
		},
		sub(){
			if(this.selectValue > 1){
				this.selectValue --;
				this.$emit('input',this.selectValue);
			} 
		}
	}
}
